import { format, parseISO } from "date-fns";
import React from "react";
import { Helmet } from "react-helmet";
import GlobalStyle, { BlogLayout, ColumnLayout, PostMetadata } from "../styles";
import "katex/dist/katex.min.css";


const renderChild = (child, key) => {
    return (<React.Fragment key={key}>{child}</React.Fragment>);
};

const renderMetadata = (pageContext) => {
  return (
    <PostMetadata>
      <div key={'date'}>{format(parseISO(pageContext.frontmatter.date), 'MMMM d, yyyy')}</div>
      &nbsp; &mdash; &nbsp;  
      {renderTags(pageContext)}
    </PostMetadata>
  );
}

const renderTag = (t, key) => {
  return (<span className={'tag'} key={key}>{t}</span>);
};

const renderTags = (pageContext) => {
  const tags = pageContext.tags || [];
  return (
    <div className={'tags'} key={'tags'}>
      {tags.join(', ')}
    </div>
  );
}

export default function Template(req) {
  const { children, pageContext } = req;
  return (
    <React.Fragment>
      <GlobalStyle />
      <BlogLayout>
        <Helmet title={`Davis Marques - ${pageContext.title}`} />
        <ColumnLayout>
          <h1>{pageContext.title}</h1>
          {renderMetadata(pageContext)}
          {children.map(renderChild)}
        </ColumnLayout>
      </BlogLayout>
    </React.Fragment>
  )
}
